@use "../../assets/sass/variables" as *;

.card {
  border: 2px solid rgb(219, 219, 219);
  margin: 20px auto;
  border-radius: 10px;
  padding: 20px;
  height: 400px;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  box-shadow: -3px 3px 10px #585858;

  &--winter {
    background: rgb(7, 0, 129);
    background: linear-gradient(
      0deg,
      rgba(7, 0, 129, 1) 0%,
      #5eacff 35%,
      rgba(213, 248, 255, 1) 100%
    );

    &:hover {
      border: 4px solid orange;
    }
  }

  &--summer {
    background: #f5b60f;
    background: linear-gradient(
      180deg,
      rgba(245, 182, 15, 1) 0%,
      rgba(233, 98, 22, 1) 76%,
      rgba(255, 0, 0, 1) 100%
    );

    &:hover {
      border: 4px solid #5eacff;
    }
  }

  &:hover {
    height: 392px;
    width: 242px;
  }

  &__img-container {
    width: 150px;
    height: 35%;
    // background: #eeeeee;
  }
  &__img {
    max-width: 90%;
    max-height: 90%;
  }
  &__content {
    height: 60%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__heading {
    color: black;
    font-size: 1.6rem;
    margin: 0.5rem;
  }

  &__bottom {
    display: flex;
    justify-content: space-evenly;
  }

  &__description {
    font-size: 0.9rem;
  }

  &__button {
    height: 100%;
    background-color: white;
    border: 2px solid black;
    border-radius: 20px;
    padding: 1px 28px;
    cursor: pointer;
    font-size: 1rem;

    &:hover {
      background-color: #888888;
      color: white;
      border: none;
      box-shadow: inset -2px 2px 10px black;
    }
  }
}

.long {
  font-size: 1.4rem;
}
